.advantages__wrapper {
    padding-top: 30px;
    padding-bottom: 35px;
}

.advantages__heading {
    margin-top: 0;
    margin-bottom: 7px;
    font-family: "Oswald", sans-serif;
    color: #003471;
    font-size: 19px;
    line-height: 28px;
    text-transform: uppercase;
}

.advantages__header {
    text-align: left;
}

.advantages__text {
    margin: 0;
}

.advantages__item {
    position: relative;
    padding: 11px;
    padding-left: 65px;
    background-image: url('/img/logo-blue.svg');
    background-repeat: no-repeat;
}

.advantages__item::before {
    position: absolute;
    content: '';
    display: block;
    top: 15px;
    left: 0;
}

.advantages__item--price::before {
    width: 55px;
    height: 47px;
    background-image: url('/img/price.svg');
}

.advantages__item--range::before {
    width: 55px;
    height: 55px;
    background-image: url('/img/house.svg');
}

.advantages__item--staff::before {
    background-image: url('/img/device.svg');
    width: 52px;
    height: 52px;
}

.advantages__item--delivery::before {
    background-image: url('/img/delivery.svg');
    width: 50px;
    height: 53px;
}

@media (max-width: 480px) {
    .advantages__item:not(:last-child) {
        margin-bottom: 21px;
    }
}

@media (min-width: 481px) {

    .advantages__wrapper {
        width: 1125px;
    }

    .advantages__list {
        display: grid;
        grid-template-columns: repeat(4, 259px);
        gap: 15px;
    }

    .advantages__item {
        margin-bottom: 0px;
    }
}