*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: inline-block;
  min-width: 204px;
  font-family: "Inter", sans-serif;
  padding: 20.5px 35px;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  line-height: 19px;
  border-radius: 3px;
  border: none;
  background-color: rgb(255, 255, 255);
  color: rgb(33, 33, 33);
  cursor: pointer;
}

.button-small {
  padding: 16px 10px;
  min-width: 193px;
  display: inline-block;
  font-family: "Inter", sans-serif;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  line-height: 19px;
  border-radius: 3px;
  border: none;
  background-color: rgb(255, 255, 255);
  color: rgb(33, 33, 33);
  cursor: pointer;
}

.button:hover, .button-small:hover {
  background-color: #cdcdcd;
}

.btn-mail {
  display: block;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  border: none;
  background-color: #000000;
  background-image: url("/img/whatsapp.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.button-grey {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

.button-grey:hover {
  background-color: #666666;
  color: #FFFFFF;
}

