    position: absolute;
    right: 0;
    top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 12px;
    height: 12px;
    font-size: 10px;
    background-color: gold;
    border-radius: 50%;
  }
}
.main-nav__toggle {
  position: relative;
  border: none;
  background-color: transparent;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 8px;
  left: 5px;
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
  right: auto;
  left: 0;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 14px;
  left: 4px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

