*,
*::after,
*::before {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.button {
    display: inline-block;
    min-width: 204px;
    font-family: 'Inter', sans-serif;
    padding: 20.5px 35px;
    text-decoration: none;
    text-align: center;
    font-size: 16px;
    line-height: 19px;
    border-radius: 3px;
    border: none;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(33, 33, 33, 1);
    cursor: pointer;
}

.button-small {
    padding: 16px 10px;
    min-width: 193px;
    display: inline-block;
    font-family: 'Inter', sans-serif;
    text-decoration: none;
    text-align: center;
    font-size: 16px;
    line-height: 19px;
    border-radius: 3px;
    border: none;
    background-color: rgba(255, 255, 255, 1);
    color: rgba(33, 33, 33, 1);
    cursor: pointer;
}

.button:hover, .button-small:hover {
    background-color: #cdcdcd;
}

.btn-mail {
    display: block;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    border: none;
    background-color: #000000;
    background-image: url('/img/whatsapp.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.button-grey {
    display: block;
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

.button-grey:hover {
    background-color: #666666;
    color: #FFFFFF;
}

@media (min-width: 481px) {
    .button-grey {
        padding: 16px;
    }
}

@media (min-width: 481px) {
    .page-header .btn-mail__text {
        display: none;
    }

    .user-list {
        display: none;
    }
}

.page {
    height: 100%;
    font-family: "Inter", sans-serif;
    font-size: 16px;
    line-height: 23.2px;
    font-weight: 400;
}

.page__body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    margin: 0;
    background-color: #FAFAFA;
    color: rgba(33, 33, 33, 1);
}

.page-main {
    flex-grow: 1;
}

.wrapper {
    width: 320px;
    padding: 0 10px;
    margin: 0 auto;
}

@media (min-width: 481px) {
    .wrapper {
        width: 1200px;
        padding: 0;
        margin: 0 auto;
    }
}

.section {
    text-align: center;
}

@media (min-width: 481px) {
    .section {
        text-align: left;
    }
}

.section__text {
    margin-top: 0;
    font-size: 24px;
    line-height: 38px;
    text-align: center;
}

.section__title {
    margin-top: 0;
    font-family: "Oswald", sans-serif;
    font-size: 26px;
    line-height: 38px;
    text-align: center;
    text-transform: uppercase;
}

@media (min-width: 481px) {
    .section__title {
        font-size: 32px;
        line-height: 46px;
        text-align: left;
    }
}

@media (min-width: 481px) {
    .section__text {
        line-height: 35px;
        text-align: left;
    } 
}