.promo {
    background-color: #003471;
    color: #FFFFFF;
    text-align: center;
    background-image: url('/img/promo-bgr-small.png');
    background-size: cover;
}

.promo__wrapper {
    width: 320px;
    margin: 0 auto;
    padding: 20px 10px 300px 10px;
}

@media (min-width: 481px) {
    .promo__wrapper {
        padding-top: 30px;
        padding-bottom: 30px;
        padding-right: 550px;
        padding-left: 45px;
        width: 1125px;
    }

    .promo {
        max-width: 1125px;
        background-image: url('/img/bgr-promo-big.png');
        margin: 0 auto;
        text-align: left;
    }

    .promo__controls {
        flex-direction: column;
        order: 3;
    }

    .promo-grid {
        display: flex;
        align-items: start;
        height: 70px;
    }

    .promo__button {
        margin-right: 60px;
        text-align: center;
    }
}

.promo__intro {
    margin: 0px;
    margin-bottom: 5px;
    color: #4CAF50;
    font-size: 14px;
    line-height: 20px;
    text-transform: uppercase;
}

.promo__heading {
    margin: 0;
    margin-bottom: 10px;
    font-family: "Oswald", sans-serif;
    font-size: 29px;
    line-height: 42px;
    font-weight: 500;
    text-transform: uppercase;
}

.promo__text {
    margin: 0;
    color: rgba(255, 255, 255, 0.8);
}

.promo__control-label {
    font-family: "Oswald", sans-serif;
    font-size: 15px;
    line-height: 22px;
    text-transform: uppercase;
}

.promo__controls {
    display: flex;
    justify-content: center;
    margin-top: 0;
    margin-bottom: 25px;
}

.promo__description {
    margin: 0;
    margin-bottom: 25px;
}

.promo__control-label {
    padding-left: 37px;
    background-image: url("../img/checked.svg");
    background-repeat: no-repeat;
    background-position: 0% center;
}

@media (max-width: 480px) {
    .promo__control-label + .promo__control-label {
        margin-left: 25px;
    }
}

@media (min-width: 481px) {
    .promo__intro {
        font-size: 21px;
        line-height: 31px;
    }

    .promo__heading {
        font-size: 39px;
        line-height: 57px;
    }

    .promo__text {
        font-size: 15px;
        line-height: 22px;
        margin-bottom: 0;
    }

    .promo__control-label {
        font-size: 20px;
        line-height: 29px;
    }

    .promo__description {
        margin-top: 0;
        margin-bottom: 20px;
    }
}