.reviews__wrapper {
    padding-top: 30px;
    padding-bottom: 35px;
}

@media (max-width: 480px) {
    .reviews__wrapper {
        position: relative;
    }
}

.reviews__text {
    margin-bottom: 8px;
}

.reviews__item {
    width: 300px;
    height: 274px;
    margin-bottom: 17px;
    padding: 30px 23px;
    background-color: #FFFFFF;
    text-align: left;
    filter: drop-shadow(4px 4px 10px rgba(0, 0, 0, 0.1));
}

@media (max-width: 480px) {
    .reviews__item:not(:first-child) {
        display: none;
    }
}

.reviews__name {
    margin-top: 0;
    margin-bottom: 30px;
    font-family: "Oswald", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
}

.reviews__link {
    color: #212121;
    text-decoration: none;
    font-size: 18px;
    line-height: 26px;
    padding: 10px;
    padding-right: 35px;
    background-image: url('/img/arrow2.svg');
    background-repeat: no-repeat;
    background-position: 120px center;
    cursor: pointer;
}

.reviews__link:hover {
    color: #cdcdcd;
}

.reviews__link:focus {
    outline: 1px solid #cdcdcd;
}

.slider__button {
    width: 24px;
    height: 27px;
    color: #003471;
    background-color: transparent;
    border: none;
    cursor: pointer;
    background-image: url('/img/arrow-prev.svg');
    background-position: center;
    background-repeat: no-repeat;
}

.slider__button:hover {
    background-color: #cdcdcd;
}

.slider__button:focus {
    outline: 2px solid #cdcdcd;
}

.button-next {
    background-image: url('/img/arrow-next.svg');
}

.slider__toggle {
    display: block;
    width: 10px;
    height: 10px;
    margin: 0;
    padding: 0;
    background-color: #C4C4C4;
    border: none;
    border-radius: 50%;
    cursor: pointer;
}

.slider__toggle:hover {
    background-color: #cdcdcd;
}

.slider__toggle:focus {
    outline: 1px solid #cdcdcd;
}

.slider__toggle--current {
    background-color: #003471;
}

.slider-pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: absolute;
    top: 10px;
    left: 125px;
}

.slider-pagination-item {
    margin: 0 4px;
}

.slider-controls {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 25px;
}

.slider-toggle-set {
    position: relative;
}

@media (min-width: 481px) {
    .reviews__wrapper {
        position: relative;
        width: 1125px;
    }
    .slider-toggle-set {
        display: none;
    }

    .reviews__item  {
        width: 358px;
        height: auto;
    }

    .reviews__list {
        display: grid;
        grid-template-columns: repeat(3, 358px);
        gap: 25px;
    }

    .reviews__link {
        position: absolute;
        top: 30px;
        right: 0;
    }
}