.page-footer__wrapper {
    padding-top: 44px;
    padding-bottom: 50px;
}

.page-footer__logo {
    text-decoration: none;
}

.page-footer__logo-image {
    margin: 0 auto;
    margin-bottom: 35px;
}

.page-footer .logo-svg {
    width: 63px;
    height: 73px;
}

.footer-logo-text {
    width: 250px;
    height: 73px;
}

.page-footer__logo-svg {
    margin-right: 17px;
}

.page-footer {
    background-color: #212121;
    color: #FFFFFF;
    text-align: center;
}

.copyright {
    font-size: 13px;
    line-height: 15px;
    color: rgba(255, 255, 255, 0.4);
}

.menu__link {
    display: block;
    padding: 3px;
    color: inherit;
    text-decoration: none;
}

.menu__link:hover, .page-footer__tel:hover, .page-footer__mail:hover {
    color: #666666;
}

.page-footer__menu {
    margin-bottom: 35px;
}

.page-footer__address {
    font-style: normal;
    margin-bottom: 29px;
    text-align: left;
}

.page-footer__address-item {
    padding: 3px;
    padding-left: 28px;
    background-repeat: no-repeat;
    display: block;
}

.page-footer__address-text {
    margin: 0;
    margin-bottom: 10px;
    background-image: url('/img/geo-pin.svg');
}

.page-footer__tel {
    margin-bottom: 8px;
    background-image: url('/img/phone.svg');
}

.page-footer__mail {
    background-image: url('/img/hour.svg');
}

.page-footer__address a {
    color: #FFFFFF;
    text-decoration: none;
}

.page-footer__btn {
    margin-bottom: 14px;
}

.copyright p {
    margin: 0;
}

.copyright p:first-child {
    margin-bottom: 5px;
}

@media (min-width: 481px) {
    .page-footer__wrapper {
        width: 1200px;
        display: grid;
        grid-template-columns: 2fr 1fr 1fr;
        grid-template-rows: min-content min-content;
    }

    .page-footer__logo {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
        height: 73px;
        justify-self: left;
    }

    .page-footer__logo-image {
        margin: 0;
    }

    .logo-image {
        width: 331px;
        height: 73px;
    }

    .copyright {
        grid-column: 1 / 2;
        justify-self: left;
        align-self: end;
        text-align: left;
    }

    .page-footer__grid-item {
        grid-column: 3 / 4;
        grid-row: 1 / 3;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: left;
    }

    .page-footer__menu {
        margin: 0;
        grid-column: 2 / 3;
        grid-row: 1 / -1;
        text-align: left;
    }

    .page-footer__btn {
        margin: 0;
    }

    .btn-mail__text {
        display: none;
    }

    .page-header-button-grid {
        display: flex;
        align-items: center;
    }

    .page-header-button-grid::after {
        content: "";
        display: block;
        width: 33px;
        height: 33px;
        background-image: url("../img/wb-icon.svg");
    }
}

@media (max-width: 480px) {
    .footer-btn-phone {
        justify-content: center;
        margin-bottom: 14px;
    }

    .page-footer .logo-svg {
        width: 50px;
        height: 58px;
    }

    .footer-logo-text {
        width: 178px;
        height: 58px;
    }

    .page-footer__logo-svg {
        margin-right: 13px;
    }
}