.page-header {
    display: flex;
    flex-direction: column;
}

.logo-image {
    display: block;
}

.page-header__logo {
    text-decoration: none;
}

@media (max-width: 480px) {
    .page-header__logo-image {
        margin-bottom: 32px;
    }  

    .main-nav {
        order: -1;
    }

    .logo-svg {
        width:  51px;
        height: 59px;
    }

    .logo-text {
        width: 203px;
        height: 60px;
    }

    .page-header__logo-svg {
        margin-right: 13px;
    } 
}

@media (min-width: 481px) {
    .page-header__logo-image {
        margin-right: 40px;
    }

    .page-header__logo-svg {
        margin-right: 21px;
    } 
    
    .logo-svg {
        width: 77px;
        height: 89px;
    }
}

@media (max-width: 480px) {
    .contacts-phone {
        margin-bottom: 16px;
    }
}

.intro {
    background-color: #111111;
    background-image: url("../img/header-bgr-mob.png"), url('../img/bgr-leather.png');
    background-repeat: no-repeat;
    background-position: 50% 100%, 50% 50%;
    background-size: contain, cover;
}

@media (max-width: 480px) {
    
}

@media (min-width: 481px) {

    .intro {
        background-color: #111111;
        background-image: url("../img/new-bgr.png"), url("../img/bgr-leather-big.png");
        background-repeat: no-repeat;
        background-position: 130% 100%, 50% 50%;
        background-size: contain, cover;
    }
    
}

.intro__wrapper {
    margin: 0 auto;
    padding-top: 43px;
    display: flex;
    align-items: center;
}

@media (min-width: 481px) {
    .intro__wrapper {
        max-width: 1200px;
        padding-bottom: 52px;
    }
}

@media (max-width: 480px) {
    .intro__wrapper {
        flex-direction: column;
        padding-bottom: 197px;
    }

    .logo-image {
        width: 268px;
        height: 59px;
        margin-bottom: 32px;
    }
}

@media (min-width: 481px) {
    .page-header__btn {
        margin-left: 16px;
    }
}

@media (max-width: 480px) {
    .page-header__btn {
        margin-right: 10px;
    }

    .page-header__button {
        margin-bottom: 21px;
    }
}

@media (min-width: 481px) {
    .page-header__contacts-address {
        margin-right: 32px;
    }

    .contacts-address {
        text-align: right;
    }
}

@media (max-width: 480px) {
    .page-header__contacts-address {
        margin-bottom: 20px;
    }

    .contacts-address {
        text-align: center;
    }
}

.btn-mail__text {
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    line-height: 17px;
}

.contacts-address {
    font-style: normal;
}

.contacts-phone {
    display: block;
    font-family: 'Oswald', sans-serif;
    color: #FFFFFF;
    font-size: 29px;
    line-height: 42px;
    text-decoration: none;
}

.contacts-address-text {
    margin: 0;
    color: rgba(255, 255, 255, 0.7);
    font-size: 15px;
    line-height: 22px;
    font-family: 'Inter', sans-serif;
}

.btn-mail-flex {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media (min-width: 481px) {
    .main-nav {
        background-image: linear-gradient(#003471, #212121);
    }

    .intro__wrapper::after {
        content: "";
        display: block;
        width: 33px;
        height: 33px;
        background-image: url('../img/wb-icon.svg');
    }

    .page-header__button {
        margin-right: 18px;
    }

    .btn-mail__text {
        display: none;
    }
}

@media (max-width: 480px) {
    .main-nav {
        background-color: #FFFFFF;

    }

    .btn-mail__text {
        display: flex;
        align-items: center;
    }

    .btn-mail__text::after {
        content: "";
        display: block;
        width: 33px;
        height: 33px;
        background-image: url('../img/wb-icon.svg');
        margin-left: 10px;
    }
}

@media (max-width: 480px) {
    .site-list {
        position: absolute;
        z-index: 15;
        width: 100%;
        flex-direction: column;
    }

    .site-list__item {
        background-image: linear-gradient(#003471, #212121);
    }

    .user-list {
        margin: 0 auto;
        max-width: 320px;
        padding: 8px;
        display: flex;
        justify-content: end;
    }

    .main-nav--closed .site-list__item {
        display: none;
    }

    .user-list__item {
        display: block;
        width: 30px;
        height: 30px;
        background-position: center;
        background-repeat: no-repeat;
    }

    .user-list__item:not(:last-child) {
        margin-right: 10px;
    }

    .user-list__item--chosen {
        background-image: url("/img/favorite2.svg");
    }

    .user-list__item--cart {
        position: relative;
        background-image: url("/img/bag.svg");
    }

    .cart-count {
        position: absolute;
        right: 0;
        top: 5px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 12px;
        height: 12px;
        font-size: 10px;
        background-color: gold;
        border-radius: 50%;
    }
}

.main-nav__toggle {
    position: relative;
    border: none;
    background-color: transparent;
}

.main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 5px;
    width: 20px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle {
    top: 0;
    right: auto;
    left: 0;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 14px;
    left: 4px;
    width: 24px;
    height: 2px;
    background-color: #000000;
}

.main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
}

.main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
}

@media (max-width: 480px) {
    .main-nav--closed .site-list__item {
        display: none;
    }
}

@media (min-width: 481px) {
    .site-list {
        flex-direction: row;
        flex-wrap: wrap;
    }

    .site-list__item:not(:last-child) {
        margin-right: 10px;
    }
}

.site-list {
    display: flex;
    justify-content: center;
}

.site-list__link {
    display: block;
    text-decoration: none;
    color: #FFFFFF;
    padding: 12px 10px;
}

.site-list__link:hover {
    color:#666666;
}

@media (max-width: 480px) {
    .site-list__link {
        background-color: #003471;
        border-bottom: 1px solid #212121;
        border-top: 1px solid #666666;
        padding-left: 21px;
    }
}

.site-list__link--active {
    color: rgba(255, 255, 255, 0.6);
}

.form__wrapper {
    padding-top: 33px;
    padding-bottom: 33px;
}

.search-form {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 79px;
}

@media (max-width: 480px) {
    .search-form {
        width: 300px;
    }
}

.search-input {
    margin: 0;
}

.field {
    width: 100%;
    margin: 0;
    padding-top: 13px;
    padding-bottom: 11px;
    padding-left: 40px;
    background-color: #FFFFFF;
    border: solid 1px #C4C4C4;
    border-right: none;
    font-family: 12px;
}

.search-button {
    background-color: #C4C4C4;
    border: none;
    cursor: pointer;
}

.search-button:hover {
    background-color: #666666;
    color: #FFFFFF;
}

.field::placeholder {
    color: rgba(0, 0, 0, 0.6);
}

.field--search {
    background-image: url("/img/search.svg");
    background-repeat: no-repeat;
    background-position: 18px center;
}

@media (min-width: 481px) {
    .form__wrapper {
        width: 1125px;
        padding-top: 23px;
        padding-bottom: 38px;
    }

    .search-form {
        grid-template-columns: 1fr 157px;
    }
}