.catalog__wrapper {
    padding-top: 20px;
}

.product-list {
    display: grid;
    grid-template-columns: 143px 143px;
    gap: 14px;
    margin-bottom: 17px;
}

.product-list picture:hover {
    opacity: 0.6;
    scale: 1.1;
}

.product-card {
    background-color: #ECECEC;
    border: 1px solid rgba(0, 0, 0, 0.2);
    overflow: hidden;
}

.product-card__title {
    margin: 0;
    font-size: 16px;
    line-height: 23px;
    color:rgba(33, 33, 33, 1);
    font-weight: 400;
    text-align: center;
    order: 3;
    align-self: center;
}

.product-card__image {
    display: block;
    order: 2;

    @media (min-width: 481px) {
        width: 222px;
        height: 157px;
    }
}

.product-card__link {
    display: grid;
    grid-template-rows: 100px minmax(57px, auto);
    background-color: #ECECEC;
    text-decoration: none;
}

.product-card__button {
    margin-bottom: 28px;
    background-color: #ECECEC;
    line-height: 23px;
    text-align: center;
}

@media (min-width: 481px) {
    .catalog__wrapper {
        padding-top: 41px;
    } 

    .product-list {
        grid-template-columns: repeat(5, 222px);
        column-gap: 23px;
        row-gap: 19px;
        margin-bottom: 20px;
    }

    .product-card__link  {
        grid-template-rows: 156px minmax(64px, auto);
    }

    .product-card__button {
        margin-bottom: 24px;
    }
}

/* ABOUT_COMPANY */

.about-company__description {
    margin-top: 0;
    text-align: center;
}

.about-company__description--bold {
    margin-top: 0;
    text-align: center;
    font-family: "Oswald", sans-serif;
    font-size: 13px;
    line-height: 19px;
}

.our-clients-list {
    display: grid;
    grid-template-columns: 150px 150px;
    align-items: center;
}

.about-company::after {
    content: "";
    display: block;
    background-image: url("/img/bgr-big-2.jpg");
    background-color: #4CAF50;
    height: 374px;
    background-size: cover;
}

@media (min-width: 481px) {
    .about-company__wrapper {
        width: 1125px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        text-align: left;
    }

    .about-company-grid {
        padding-right: 30px;
    }

    .about-company-grid-bgr {
        background-image: url("/img/bgr-big-2.jpg");
        background-size: cover;
    }

    .about-company::after  {
        display: none;
    }

    .about-company__description--bold {
        font-family: 16px;
        line-height: 23px;
        text-align: left;
    }

    .about-company__description {
        text-align: left;
    }

    .our-clients-list {
        grid-template-columns: repeat(4, 1fr);
    }
}